.footer {
    width: 100%;
    padding: 6rem 0;
    background-color: rgba(0, 0, 0, 0.8);
}

.footer-container {
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
}

.footer .left {
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.footer .right {
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.footer h4 {
    font-size: 1.4rem;
    padding: 0 0 1rem;
}

.footer p {
    font-size: 1.2rem;
    padding: 0.5rem 0;
}

.footer .location {
    display: flex;
    align-items: center;
}

.footer .location p {
    padding-top: 0;
}

.footer .social {
    padding-top: 1rem;
}

@media screen and (max-width:640px) {
    .footer-container {
        grid-template-columns: 1fr;
    }
}