@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@100;300;400;500;600;700;800&display=swap);

.header {
    position: fixed;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    z-index: 10;
    background-color: #0000000d;
}

.header-dark {
    background-color: rgba(0, 0, 0, 0.85);
    transition: 0.5s;
}

#logo-company {
    font-size: 2.5rem;
    font-weight: 600;
}

.nav-menu {
    display: flex;
}

.nav-menu li {
    padding: 0 1rem;
}

.nav-menu a {
    font-size: 1.2rem;
    font-weight: 500;
    text-transform: uppercase;
}

.hamburger {
    display: none;
}

@media screen and (max-width: 1240px) {
    .hamburger {
        display: block;
        cursor: pointer;
    }

    #logo-company {
        font-size: 2rem;
    }

    .nav-menu {
        position: absolute;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        text-align: center;
        width: 100%;
        height: 100vh;
        transition: 0.3s;
        z-index: -1;
        background-color: rgba(0, 0, 0, 0.9);
    }

    .nav-menu.active {
        left: 0;
    }

    .nav-menu li {
        padding: 1rem 0;
    }
    .nav-menu a {
        font-size: 2rem;
    }
}
.footer {
    width: 100%;
    padding: 6rem 0;
    background-color: rgba(0, 0, 0, 0.8);
}

.footer-container {
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
}

.footer .left {
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.footer .right {
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.footer h4 {
    font-size: 1.4rem;
    padding: 0 0 1rem;
}

.footer p {
    font-size: 1.2rem;
    padding: 0.5rem 0;
}

.footer .location {
    display: flex;
    align-items: center;
}

.footer .location p {
    padding-top: 0;
}

.footer .social {
    padding-top: 1rem;
}

@media screen and (max-width:640px) {
    .footer-container {
        grid-template-columns: 1fr;
    }
}
#video {
    position: fixed;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
}

.hero {
    height: 100vh;
    width: 100%;
    object-fit: contain;
}

.hero .content {
    text-align: center;
    position: absolute;
    margin: auto;
    width: 100%;
    height: 100%;
    top: 40vh;
}

.hero .content h1 {
    font-size: 4rem;
}

.hero .content p {
    font-size: 1.8rem;
    font-weight: 200;
    text-transform: uppercase;
    margin-bottom: 1.6rem;
}

.hero .btn {
    margin: 1rem 0.2rem;
}

@media screen and (max-width:640px) {
    .content h1 {
        font-size: 3rem;
    }

    .content p {
        font-size: 1.4rem;
        margin-bottom: 1.6rem;
    }
}
.hero-img {
    background-color: rgba(0, 0, 0, 0.3);
    height: 50vh;
    width: 100%;
    position: relative;
}

.hero-img::before {
    content: '';
    background-image: url(/law-company/static/media/unsplash-min.cd043d95.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

.hero-img .heading {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.pricing {
    width: 100%;
    padding: 6rem 1rem;
    background-color: #020202;
}

.card-container {
    max-width: 1440px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
    margin: 0 auto;
}

.card {
    text-align: center;
    border: 1px solid #ffffff9a;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.card:hover {
    background-color: rgb(22, 31, 55);
}

.card-title {
    font-size: 1.3rem;
    font-weight: 400;
    color: #ffffff;
    text-transform: uppercase;
    letter-spacing: 0.03em;
}

.card p {
    font-weight: 300;
    padding: 0.5rem 0 0.5rem 1.5rem;
    text-align: left;
    position: relative;
}

.card p::before {
    position: absolute;
    content: '-';
    left: 0;
    top: 0.5rem;
    color: #ffffff;
}

.card .btn {
    margin: 2rem auto;
    display: block;
    width: 80%;
    color: #ffffff;
}

.card .bar {
    border-bottom: 1px solid #eeeeee;
    width: 17%;
    margin: 1.5rem auto;
    display: block;
}

@media screen and (max-width: 640px) {
    .card-container {
        grid-template-columns: 1fr;
        margin: 0 auto;
        max-width: 90%;
    }
}
.training {
  width: 100%;
  margin: 5rem 0 3rem 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  height: 100%;
}

.training-left {
  text-align: center;
  padding: 1rem 4rem;
  max-width: 680px;
}

.training-left p {
  margin: 1.2rem 0;
}

.training-right {
  width: 100%;
  text-align: center;
}

.training-right .img-container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(2, 1fr);
  position: relative;
  align-items: flex-start;
  justify-items: center;
}

.img-container .top {
  grid-column: 1 / span 8;
  grid-row: 1;
  z-index: 6;
}

.img-container .bottom {
  grid-column: 3 / -1;
  grid-row: 2;
  z-index: 5;
  margin-top: -63%;
}

.foto1 {
  max-width: 60%;
  border: 1px solid #959595;
}

.foto2 {
  max-width: 60%;
  border: 1px solid #333333;
}

@media screen and (max-width: 1140px) {
  .training {
    grid-template-columns: 1fr;
  }

  .training-right img {
    max-width: 80%;
  }
}
form {
  display: flex;
  flex-direction: column;
  padding: 5rem 1rem 1rem 1rem;
  margin: auto;
  max-width: 600px;
}

form label {
  margin-bottom: 0.5rem;
  color: #ffffff;
}

input, textarea {
  margin-bottom: 1rem;
  padding: 10px;
  font-size: 1.2rem;
  background-color: #ffffff23;
  outline: none;
  border: 1px solid #454545;
  font-family: 'Outfit', sans-serif;
  color: #f4f4f4;
}
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Outfit', 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #000000;
}

h1, h4, p, a {
    color: #ffffff;
}

h1 {
    font-size: 2.4rem;
}

p {
    font-size: 1.4rem;
}

ul {
    list-style-type: none;
}

a {
    text-decoration: none;
}

.btn {
    padding: 12px 32px;
    font-size: 1.2rem;
    text-transform: uppercase;
    background-color: transparent;
    color: #ffffff;
    border: 1px solid #ffffff;
    cursor: pointer;
}

.btn-light {
    background-color: rgba(255, 255, 255, 0.2);
}

.btn:hover {
    background-color: rgba(255, 255, 255, 0.2);
}

@media screen and (max-width:640px) {
    h1 {
        font-size: 2rem;
    }
}
